import { SectionHeading } from "components/misc/Headings";
import tw from "twin.macro";

import ProjectLeft from "components/cards/ProjectLeft"
import ProjectRight from "components/cards/ProjectRight"
import projects_info from "project_info.json"
import AnimationRevealPage from "helpers/AnimationRevealPage"
import { SectionDescription } from "components/misc/Typography";

const Heading = tw(SectionHeading)``;
const Description = tw(SectionDescription)`max-w-full text-center mb-24 mt-8 text-gray-900 lg:text-base mx-auto lg:mx-0`;
const description_text = "This section contains an overview of the projects I worked on in the last years."


function leftOrRight(index, project_data) {
    if (index == 0) {
        return (
            <div>
                <Heading>Projects</Heading>
                <Description>This section contains an overview of the personal/professional projects I worked on in the last years. The projects are ordered by relevance/size. <br/> Note: University course projects are not listed here, with some exceptions.</Description>
                <ProjectLeft project_data={project_data}/>
            </div>
        )
    }
    console.log(project_data.images[project_data.display_image_index].imageSrc)
    if (index % 2 == 0) {
        
        return <ProjectLeft project_data={project_data}/>
    } else {
        return <ProjectRight project_data={project_data}/>
    }
}


export default ({ 
}) => {
    const p = projects_info["projects"]
    return (
        <AnimationRevealPage>
            <div id="projects">
                {Object.keys(p).map((item, index) => (
                    leftOrRight(index, p[item])
                ))}
            </div>
            
        </AnimationRevealPage>

    );
};


  
  